}
.gt-thumb {
  position: relative;
  padding: 40px;
}
.gt-thumb:before {
  content: "";
  width: 250px;
  height: 250px;
  background: url(https://ttplus.com.vn/wp-content/uploads/2021/04/102254217-seamless-pattern-from-crossed-lines-endless-background-from-crosses.jpg);
  background-size: contain;
  position: absolute;
  bottom: 6px;
  right: 0px;
  display: inline-block;
  z-index: -1;
}

.prod-dt-cate .slider-prod .slick-arrow::before {
  display: none;
}

.ttplus-section {
  padding: 90px 0;
}

@media screen and (max-width: 767px) {
  .ttplus-section {
    padding: 30px 0;
  }
}
.ttplus-title {
  margin-bottom: 40px;
}

.arck-project-filter-btn .button-group {
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.arck-project-filter-btn .button-group .filter-button {
  padding: 5px 10px;
  width: auto;
}

@media (min-width: 1400px) {
  .container,
.container-lg,
.container-md,
.container-sm,
.container-xl,
.container-xxl {
    max-width: 1440px;
  }
}
.arck-sponsor-slider .arck-sponsor-slider-item {
  padding-left: 20px;
  padding-right: 20px;
}

.header-cta {
  font-size: 20px;
  display: flex;
  align-items: center;
}

.header-cta-btn {
  padding: 8px 15px;
  cursor: pointer;
}
.header-cta-btn:hover {
  color: white;
}
.header-cta-btn .dropdown-toggle {
  color: white;
}

.sticky-on .dropdown-toggle {
  color: rgba(0, 0, 0, 0.9);
}

.hder-section .hd-close .btn-hd-close {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  width: 60px;
  height: 60px;
  background: url(https://img.lxhausys.com/public/images/header/btn_header_close.png) no-repeat center center #f2f2f2;
}

.sch-top .sch-frm .btn-hd-sch {
  display: inline-block;
  width: 55px;
  height: 27px;
  background: url(https://img.lxhausys.com/public/images/header/btn_header_search.png) no-repeat center center;
}

.ft-title {
  color: white;
  padding-top: 20px;
  padding-bottom: 20px;
}

.an-hung-footer {
  color: rgba(255, 255, 255, 0.6);
}

.ft-address {
  font-size: 14px;
  margin-top: 15px;
}

@media screen and (min-width: 1200px) {
  .container.sub {
    max-width: 1200px;
  }
}
.ttplus-section-bg-dark {
  position: relative;
  z-index: 1;
  background-position: center center;
}
.ttplus-section-bg-dark:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.3);
}

.text-black {
  color: rgba(0, 0, 0, 0.9);
}

.arck-project-item:before {
  display: none;
}
.arck-project-item .inner-img {
  position: relative;
}
.arck-project-item .detail-link {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  z-index: 2;
  padding: 20px;
  align-items: flex-end;
  justify-content: center;
}
.arck-project-item .name {
  text-align: center;
  padding: 10px 0;
  font-weight: bold;
}

.menu-box ul {
  padding-left: 0;
}

.menu-box ul li {
  text-align: left;
}

.gioithieu-bg-logo {
  opacity: 0.1;
  z-index: -1;
  filter: grayscale(100%);
}

.businessList .bgArea {
  height: 300px;
}

.project-link-over {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  background-color: rgba(0, 0, 0, 0.3);
  font-size: 20px;
  font-weight: bold;
  padding: 20px;
  text-align: center;
}
.project-link-over:hover, .project-link-over:focus, .project-link-over:active {
  color: white;
}

.recent-blog-img {
  flex-shrink: 0;
}

.btn2-anim {
  position: relative;
  width: auto;
  padding: 0 19px;
  transition: all 0.5s ease-out 1.8s;
  height: 44px;
  min-height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn2-anim:after {
  content: "";
  position: absolute;
  left: 0px;
  top: 0px;
  width: 44px;
  height: 100%;
  border-radius: 30px;
  background: #000;
  opacity: 0.2;
  transition: all 0.45s ease-in-out 0s;
}
.btn2-anim:hover::after {
  width: 100%;
  opacity: 0.8;
}
.btn2-anim span {
  font-weight: bold;
  position: relative;
  display: block;
  padding-right: 41px;
  background: url(../img/arrw_right_04.png) right center no-repeat;
  font-size: 15px;
  line-height: 33px;
  color: #fff;
  z-index: 100;
  white-space: nowrap;
}

.arck-project-item:hover .btn2-anim:after {
  width: 100%;
  opacity: 0.8;
}

.arck-project-item-overlay .inner-img:after {
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  content: "";
  height: 100%;
  position: absolute;
  transition: var(--transition);
  background-color: rgba(0, 0, 0, 0.2);
}

.ttplus-s1 {
  margin-top: 40px;
  padding-right: 110px;
  position: relative;
}

.ttplus-s1-icon {
  position: absolute;
  width: 80px;
  right: 0;
  top: 10px;
  padding: 14px;
  background-color: var(--white-color);
  box-shadow: 0px 0px 10px 0 rgba(0, 0, 0, 0.1);
}

.ttplus-s1-tt {
  display: flex;
  align-items: center;
}
.ttplus-s1-tt .heading {
  width: 90px;
  flex-shrink: 0;
  margin-bottom: 0;
  font-size: 22px;
}
.ttplus-s1-tt .dash {
  flex-grow: 1;
  height: 2px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}

@media screen and (max-width: 767px) {
  .ttplus-s1-tt .heading {
    width: 80px;
    font-size: 18px;
  }
}